import * as v from 'valibot';
import { PaymentCurrencyEnum, RecieptDeliveryEnum } from '../../models/payment';
var t = function (key) { return key; };
var getKey = function (path) { return "validation:common.".concat(path); };
var PaymentAccountSchema = v.pipe(v.object({ id: v.union([v.number(), v.string()]), label: v.string() }), v.check(function (input) { return input !== null; }, t(getKey('predefined'))));
var EmailSchema = v.union([
    v.pipe(v.string(t(getKey('invalidString'))), v.trim(), v.nonEmpty(t(getKey('required'))), v.email(t(getKey('invalidEmail')))),
    v.object({
        id: v.string(t(getKey('invalidString'))),
        label: v.string(t(getKey('invalidString'))),
        owner: v.string(),
    }),
], t(getKey('required')));
var PhoneSchema = v.union([
    v.pipe(v.string(t(getKey('invalidString'))), v.trim(), v.nonEmpty(t(getKey('required')))),
    v.object({
        id: v.string(t(getKey('invalidString'))),
        label: v.string(t(getKey('invalidString'))),
        owner: v.string(),
    }),
], t(getKey('required')));
export var GeneratePaymentFormSchema = v.object({
    description: v.pipe(v.string(t(getKey('invalidString'))), v.trim(), v.nonEmpty(t(getKey('required')))),
    amount: v.pipe(v.string(t(getKey('invalidString'))), v.trim(), v.nonEmpty(t(getKey('required'))), v.check(function (input) { return !isNaN(Number(input)); }, function () { return t(getKey('invalidNumber')); })),
    currency: v.enum(PaymentCurrencyEnum),
    email: EmailSchema,
    phone: PhoneSchema,
    paymentAccount: PaymentAccountSchema,
    receiptDelivery: v.enum(RecieptDeliveryEnum),
});
