import { useDispatch, useSelector } from 'react-redux';
import { clearDeal, hydrateDeal } from '../store/reducers/app/actions';
import { selectDeal } from '../store/reducers/app/selectors';
export var useDeal = function () {
    var dispatch = useDispatch();
    var deal = useSelector(selectDeal);
    return {
        deal: deal,
        hydrateDeal: function (entity) { return dispatch(hydrateDeal(entity)); },
        clearDeal: function () { return dispatch(clearDeal()); },
    };
};
